body {
  background: #f3f3f3;
}

img {
  object-fit: cover;
  height: 300px;
  width: 100%;
}

a {
  color: #e74c3c;
}

button:focus {
  outline: none;
}

.App {
  text-align: center;
  position: relative;
}

.App-header {
  background-color: #3498db;
  height: 90px;
  padding: 30px;
  color: white;
  margin-bottom: 2rem;
}

.App-title {
  font-size: 1.5em;
}

form,
.col-md-4 {
  margin-bottom: 2rem;
}

.recipe_buttons {
  border: solid 2px #e74c3c;
  background: transparent;
  text-decoration: none;
  padding: 0.4rem 0.6rem;
  margin: 0 0.2rem;
  border-radius: 1px;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 0.7rem;
  transition: 0.5s ease-in;
}

.recipe_buttons:hover,
.active-recipe__button:hover {
  color: white;
  background-color: #e74c3c;
  cursor: pointer;
  text-decoration: none;
}

.ingredient_container {
  text-transform: uppercase;
}

.container {
  padding-bottom: 3em;
}

.recipes__box {
  background: #fff;
  padding-bottom: 2rem;
  border-radius: 5px;
  box-shadow: -1px 2px 34px -13px rgba(0, 0, 0, 0.75);
  text-align: left;
  height: 450px;
  overflow: hidden;
}

.recipe__text {
  margin: 0 15px;
}

.recipes__title {
  color: #e74c3c;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.1rem;
  padding: 1.8rem 0 0.2rem 0;
}

.recipes__subtitle {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.8rem;
  letter-spacing: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.recipes__subtitle span {
  text-transform: none;
  font-weight: 400;
  font-size: 0.8rem;
  letter-spacing: 1px;
}

.recipe_num,
.ingredient {
  color: #e74c3c;
  font-weight: bold;
}

.form__input {
  background: transparent;
  border: 0;
  border-bottom: solid 2px #3498db;
  margin-right: 10px;
  letter-spacing: 2px;
  width: 10rem;
  padding: 0.4rem;
}

.form__input:focus {
  border-bottom: solid 2px #34495e;
}

.form__button {
  border: solid 2px #e74c3c;
  background: transparent;
  padding: 0.2rem 0.9rem;
  margin: 0 0.2rem;
  border-radius: 1px;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 0.7rem;
}

.form__button:hover {
  cursor: pointer;
  background-color: #e74c3c;
  transition: 0.5s ease-in;
}

input[type="text"] {
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #f3f3f3 inset;
  -webkit-text-fill-color: #2c3e50 !important;
}

.sort * {
  padding: 0 5px;
  margin-bottom: 1em;
}

.sort label:nth-child(1) {
  margin-right: 10px;
}

.input-group {
  justify-content: center;
}

.input-group-prepend:nth-child(1) {
  padding-right: 20px;
}

.loading {
  height: 100px;
  position: relative;
  top: 10px;
  width: 10vw;
}

.fa-apple-alt {
  animation: loadingFade 2s infinite;
  position: absolute;
}

.fa-apple-alt:nth-child(1) {
  animation-delay: 0s;
}

.fa-apple-alt:nth-child(2) {
  animation-delay: 0.2s;
}

.fa-apple-alt:nth-child(3) {
  animation-delay: 0.4s;
}

.fa-apple-alt:nth-child(4) {
  animation-delay: 0.6s;
}

.fa-apple-alt:nth-child(5) {
  animation-delay: 0.8s;
}

@keyframes loadingFade {
  0% {
    opacity: 0;
    transform: translateX(0);
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0;
    transform: translateX(60vw);
  }
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100vw;
  height: 4em;
  color: white;
  background-color: #3498db;
}

.footer span {
  display: block;
  padding-top: 1em;
}

.footer a {
  text-decoration: none;
}
